body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  color:white;
  background-color: black;
  padding: 20px 40px;
  justify-content: space-between;
  font-size: 30px;
  display: flex;
  font-family: Brush Script MT;
  font-size: 50px;
}
.cart-icon .counter
{
  height: 18px;
  width:18px;
  background-color: rgba(173, 32, 255, 0.728);
  border-radius: 50%;
  position: relative;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  right : 12px;
  bottom: 8px;
}

.cart-icon{
  color: white;
  padding: 10px;
  display: flex;
}
.icon{
  position: relative;
  right: 2px;
  font-size: 35px;
}
img
{
  height: 150px;
  width: 150px;
}
.cart-wrapper{
  padding: 20px;
  display: inline-flex;
  margin: 40px;
  width: 300px;
  box-shadow: 5px 5px 20px black; 
  border-radius: 15px;
  transition: 0.4s ease-in-out;
}
.cart-wrapper:hover{
  transform: scale(1.07);
}
.image-wrapper{
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}
.content{
  position: relative;
  left: 40px;
  display: flex;
  flex-direction: column;
}
.price{
  position: relative;
  padding-top: 20px;
  padding-bottom: 50px;
}
.content button{
  background-color: black;
  color: aliceblue;
  border-radius:5px ;
  border: 1px;
  width: 130px;
  height: 25px;
  transition: 1s ease;
}
.content button:hover{
  background-color: #01ffaa;
  color: black;
}
.quantity{
position: relative;
bottom: 7px;
display: flex;
align-self: flex-end;
}
.quantity div{
  height: 20px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center
}
.quantity > div:first-child,
.quantity > div:last-child{
  background-color: rgba(173, 32, 255, 0.728);
}
.quantity button{
  width: 25px;
  background-color: rgba(173, 32, 255, 0.728);
}
.quantity button:disabled{
  opacity: 0.2;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.total{
  position: fixed;
  background-color: gray;
  padding: 15px;
  padding-right: 30px;
  bottom: 0;
  width:100%;
  position: relative;
  right: 40px;
  display: flex;
  justify-content: flex-end;
}
::-webkit-scrollbar{
  display: none;
}